* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  user-select: none;
}

html, body, #root, .App {
  height: 100%;
}

.App {
  background-image: url("https://t4.ftcdn.net/jpg/01/75/41/63/360_F_175416312_CLaJROwFrbqcsuVkTiH5ru5e93NGCWTE.jpg");
  background-size: cover;
  background-position: center;
}

#monster {
  width: 300px;
  height: 300px;
  background-size: 605%;
}

.center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#damage {
  width: 100%;
  height: 40px;
  border: 3px solid white;
  position: relative;
  border-radius: 23px;
}
#damage > div {
  background-color: red;
  background-image: url("https://img.freepik.com/free-photo/fire-flame-with-sparkle-black-background_84443-7822.jpg");
  height: 100%;
  background-position: center;
  color: white;
  border-radius: inherit;
}

.effectP {
  position: absolute;
  font-size: 40px;
  font-weight: bold;
  color: red;
  animation-name: effectPos;
  animation-duration: 1s;
  transform: translateX(-50%);
}

@keyframes effectPos {
  from {
    transform: translate(-50%, 0);
    opacity: 1;
  }
  to {
    transform: translate(-50%, -100px);
    opacity: 0;
  }
}
.clickEffect {
  animation-name: effect;
  animation-duration: 0.2s;
  animation-direction: alternate;
}

@keyframes effect {
  from {
    transform: translate(5px, 5px);
  }
  to {
    transform: translate(-5px, -5px);
  }
}
.message {
  font-family: "Montserrat", sans-serif;
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
  margin-bottom: 12px;
  border-radius: 10px;
  font-size: 12px;
  padding: 12px;
  width: auto;
  display: flex;
  align-items: center;
  color: white;
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  animation-name: popupMessage;
  animation-duration: 0.5s;
}

.endMessage {
  animation-name: endMessage;
}

@keyframes endMessage {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-100px);
  }
}
@keyframes popupMessage {
  from {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
#messagesGroup {
  position: fixed;
  top: 12px;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 999;
}

.smallIconMessage {
  width: 15px;
  height: 15px;
  margin-right: 5px;
}
.smallIconMessage img {
  width: 100%;
  height: 100%;
}

.flex {
  display: flex;
}

.align {
  align-items: center;
}

.coinP {
  color: white;
  font-size: 30px;
}

.coin {
  width: 41px;
  height: 41px;
  background-image: url("./images/halloweenCoins.jpg");
  background-size: 348%;
  border-radius: 50%;
  margin-left: 5px;
  display: inline-block;
}
.coin.row1 {
  background-position-y: -23px;
}
.coin.row1.small {
  background-position-y: -10px;
}

.between {
  justify-content: space-between;
}

.padding {
  padding: 0 10px;
}

.small.coin {
  width: 20px;
  height: 20px;
  min-width: 20px;
  min-height: 20px;
  margin-right: 5px;
}
.small.coin1 {
  background-position-x: -3px;
}
.small.coin2 {
  background-position: -25px;
}

.coin1 {
  background-position-x: -6px;
}

.coin2 {
  background-position: -51px;
}

